// ë§Œì•½ ì•„ì§ 'getMyPosts'ë¥¼ ì‚¬ìš© ì¤‘ì´ë¼ë©´, íŒŒì¼ ë‚´ 'getMyPosts'ë¥¼ 'getPostsByAuthor'ë¡œ ë°”ê¿”ì£¼ì„¸ìš”.
import { getPostsByAuthor } from '@/api/blogService/post';
